import React from "react";
import { useFormik } from "formik";
import { useDispatch } from "react-redux";
import { AppDispatch } from "../store";
import { Form, Button } from 'react-bootstrap';
import * as Yup from 'yup';
import { addExpenseToFirestore } from "../store/expensesSlice";

const validationSchema = Yup.object({
    category: Yup.string().required(" "),
    amount: Yup.number()
        .required(" ")
        .positive("    ")
        .typeError("   "),
    date: Yup.date().required(" "),
    quantity: Yup.number()
        .required(" ")
        .positive("    ")
        .integer("    ")
        .typeError("   "),
});

const ExpenseForm: React.FC = () => {
    const dispatch = useDispatch<AppDispatch>();

    const formik = useFormik({
        initialValues: {
            category: '',
            amount: '',
            date: new Date().toISOString().split("T")[0],
            quantity: 1,
        },
        validationSchema, 
        onSubmit: (values) => {
            const date = new Date(values.date);
            const month = date.getMonth() + 1;

            dispatch(addExpenseToFirestore({ 
                amount: +values.amount * +values.quantity,
                category: values.category, 
                date: values.date,
                quantity: +values.quantity,
                month,
            }));
            formik.resetForm();
        }
    });

    return (
        <Form onSubmit={formik.handleSubmit}>
            <Form.Group controlId="category">
                <Form.Label></Form.Label>
                <Form.Control
                    as="select"
                    name="category"
                    onChange={formik.handleChange}
                    value={formik.values.category}
                    isInvalid={!!formik.errors.category && formik.touched.category}
                >
                    <option value=""> </option>
                    <option value=" "> </option>
                    <option value=" "> </option>
                    <option value=""></option>
                    <option value=""></option>
                    <option value=""></option>
                    <option value=""></option>
                    <option value=""></option>
                </Form.Control>
                <Form.Control.Feedback type="invalid">{formik.errors.category}</Form.Control.Feedback>
            </Form.Group>

            <Form.Group controlId="amount">
                <Form.Label></Form.Label>
                <Form.Control
                    type="number"
                    name="amount"
                    onChange={formik.handleChange}
                    value={formik.values.amount}
                    placeholder=" "
                    isInvalid={!!formik.errors.amount && formik.touched.amount}
                />
                <Form.Control.Feedback type="invalid">{formik.errors.amount}</Form.Control.Feedback>
            </Form.Group>

            <Form.Group controlId="quantity">
                <Form.Label></Form.Label>
                <Form.Control
                    type="number"
                    name="quantity"
                    onChange={formik.handleChange}
                    value={formik.values.quantity}
                    placeholder=" "
                    isInvalid={!!formik.errors.quantity && formik.touched.quantity}
                />
                <Form.Control.Feedback type="invalid">{formik.errors.quantity}</Form.Control.Feedback>
            </Form.Group>

            <Form.Group controlId="date">
                <Form.Label></Form.Label>
                <Form.Control
                    type="date"
                    name="date"
                    onChange={formik.handleChange}
                    value={formik.values.date}
                    isInvalid={!!formik.errors.date && formik.touched.date}
                />
                <Form.Control.Feedback type="invalid">{formik.errors.date}</Form.Control.Feedback>
            </Form.Group>

            <Button type="submit" variant="primary" className="mt-4 w-100">
                 
            </Button>
        </Form>
    );
}

export default ExpenseForm;
